.page {
  height: 100%;
  font-size: 20px;
  font-family: "Oswald", "Arial", sans-serif;
  line-height: 20px;
}

.page__body {
  min-height: 100%;
  margin: 0;
  background-color: #ffffff;
  color: #000000;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswald/oswaldmedium.woff2") format("woff2"), url("../fonts/oswald/oswaldmedium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../fonts/lato/Lato-Regular.woff") format("woff");
  font-display: swap;
}
.button {
  border: none;
  cursor: pointer;
  background-color: #68b738;
  color: #ffffff;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  text-align: center;
  text-transform: uppercase;
}
.button:hover {
  background-color: #5eaa2f;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}

.visually-hidden {
  overflow: hidden;
  clip: rect(0 0 0 0);
  border: 0;
  padding: 0;
  margin: -1px;
  height: 1px;
  width: 1px;
  position: absolute;
}

.main-header {
  position: relative;
  width: 100%;
  margin: 0;
  padding-top: 10px;
  padding-bottom: 17px;
  display: flex;
  flex-direction: column;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  color: #000000;
  text-decoration: none;
  text-transform: uppercase;
}

.main-header--index {
  width: 320px;
  margin: 0 auto;
}

@media (min-width: 769px) {
  .main-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    min-width: 708px;
    width: 92%;
    padding: 5px 30px;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    color: #000000;
    background: #ffffff;
    text-decoration: none;
    text-transform: uppercase;
  }
}
@media (min-width: 1440px) {
  .main-header {
    position: relative;
    width: calc(100% - 220px);
    padding: 54px 110px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .main-header::before {
    content: "";
    position: absolute;
    background-image: url("../img/background-images/heroblock-background-cat.png");
    width: 720px;
    height: 694px;
    top: 0;
    right: 0;
  }
}
@media (min-width: 1440px) and (min-resolution: 2dppx) {
  .main-header::before {
    background-image: url("../img/background-images/heroblock-background-cat@2x.png");
    background-size: 720px 694px;
  }
}
@media (min-width: 1440px) {
  .main-header--form::before,
.main-header--catalog::before {
    display: none;
  }
}
.logo {
  width: calc(100% - 40px);
  padding: 0 20px;
  display: flex;
  align-self: center;
}

.logo__link {
  width: 100%;
  display: flex;
  align-items: center;
  column-gap: 20%;
}

.logo__link:hover .logo__image {
  opacity: 0.8;
}
.logo__link:hover .logo__text {
  opacity: 0.8;
}

.logo__link:active .logo__image {
  opacity: 0.5;
}
.logo__link:active .logo__text {
  opacity: 0.5;
}

.logo__text {
  height: 18px;
  width: 101px;
  text-align: center;
}

@media (min-width: 769px) {
  .logo {
    margin: 0;
    padding: 0;
    min-height: 52px;
    width: 174px;
  }

  .logo__image {
    width: 174px;
    height: 52px;
  }

  .logo__text {
    display: none;
  }
}
@media (min-width: 1440px) {
  .logo {
    margin: 0;
    width: 202px;
  }

  .logo__image {
    width: 202px;
    height: 59px;
  }
}
.main-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 400;
  color: #000000;
  background: #ffffff;
}

@media (min-width: 769px) {
  .main-container {
    align-items: center;
    min-width: 708px;
    width: 92%;
    padding: 0 30px;
    margin: 0 auto;
  }
}
@media (min-width: 1440px) {
  .main-container {
    display: flex;
    flex-direction: column;
    width: calc(100% - 220px);
    padding: 0 110px;
  }
}
.main-navigation {
  position: relative;
  width: auto;
}

.main-navigation__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.main-navigation__toggle {
  position: absolute;
  top: 0;
  right: 5%;
  z-index: 2;
  width: 30px;
  height: 30px;
  margin-top: -30px;
  background-color: #ffffff;
  cursor: pointer;
  border: none;
}
@media (min-width: 769px) {
  .main-navigation__toggle {
    display: none;
  }
}

.main-navigation--closed .site-list__item {
  display: none;
}

.main-navigation--closed .main-navigation__toggle::before {
  content: "";
  position: absolute;
  top: 5px;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}

.main-navigation--closed .main-navigation__toggle:active::before {
  background-color: #808080;
  box-shadow: 0 8px 0 0 #808080, 0 16px 0 0 #808080;
}

.main-navigation--opened .main-navigation__container {
  position: absolute;
  top: 10px;
  z-index: 10;
  background-color: #ffffff;
}

.main-navigation--opened .main-navigation__toggle::before, .main-navigation--opened .main-navigation__toggle::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
}
.main-navigation--opened .main-navigation__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-navigation--opened .main-navigation__toggle::after {
  transform: rotate(-45deg);
  box-shadow: none;
}

.main-navigation--nojs .main-navigation__toggle {
  display: none;
}

.main-navigation--nojs .main-navigation__container {
  position: static;
  min-height: 0;
}

.main-navigation--nojs .site-list__item {
  display: block;
}

@media (min-width: 769px) {
  .main-navigation {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-end;
    align-items: center;
    width: 82%;
    padding: 0;
  }

  .main-navigation__toggle {
    display: none;
  }

  .main-navigation--opened .main-navigation__container {
    position: relative;
    top: 0;
    z-index: 0;
    background-color: #ffffff;
  }
}
@media (min-width: 1440px) {
  .main-navigation {
    display: flex;
    justify-content: flex-end;
    margin: 0;
    align-items: center;
    min-width: 478px;
  }

  .main-navigation--opened .main-navigation__container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-end;
    background-color: transparent;
  }

  .main-navigation__toggle {
    display: none;
  }
}
.site-list {
  position: static;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 320px;
}

.site-list--index {
  width: 320px;
}
@media (min-width: 769px) {
  .site-list--index {
    width: auto;
  }
}

.site-list a {
  display: block;
  min-width: 100vw;
  padding-top: 22px;
  padding-bottom: 24px;
  list-style-type: none;
  text-decoration: none;
  color: #000000;
  border-bottom: 1px solid #e6e6e6;
  text-align: center;
}

.site-list__item--index {
  width: 320px;
}
@media (min-width: 769px) {
  .site-list__item--index {
    width: auto;
  }
}

.site-list__item--index a {
  min-width: 320px;
  width: 320px;
}
@media (min-width: 769px) {
  .site-list__item--index a {
    width: auto;
  }
}

.site-list__item:first-of-type {
  border-top: 1px solid #e6e6e6;
}

@media (min-width: 769px) {
  .site-list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    min-width: 440px;
    gap: 10px;
    margin: 0;
    margin-left: 20px;
  }

  .site-list__item {
    min-width: 64px;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    opacity: 1;
  }
  .site-list__item:first-of-type {
    border: none;
  }
  .site-list__item:hover {
    opacity: 0.6;
  }
  .site-list__item:focus-within {
    opacity: 0.3;
  }
  .site-list__item--current {
    position: relative;
  }
  .site-list__item--current::after {
    content: "";
    position: absolute;
    background: #68b738;
    top: 10;
    right: 0;
    width: 100%;
    height: 2px;
  }

  .site-list a {
    min-width: 64px;
    padding: 10px;
    border: none;
  }
}
@media (min-width: 1440px) {
  .site-list {
    width: 600px;
    display: flex;
    justify-content: flex-end;
  }

  .site-list__item {
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
  }
  .site-list__item--current {
    position: relative;
  }
  .site-list__item--current::after {
    content: "";
    position: absolute;
    background: #ffffff;
    top: 10;
    right: 0;
    width: 100%;
    height: 2px;
  }

  .site-list__item--current-form::after,
.site-list__item--current-catalog::after {
    background: #68b738;
  }

  .site-list a {
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
    color: #ffffff;
  }

  .site-list--form a,
.site-list--catalog a {
    color: #000000;
  }
}
.choose-programm {
  width: 100vw;
  background: linear-gradient(to bottom, rgba(104, 183, 56, 0.85) 290px, white 290px), url("../img/background-images/heroblock-background-mobile.png");
